html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

.icon-next { width: 0.68em; height: 1em; fill: initial; }

.icon-prev { width: 0.68em; height: 1em; fill: initial; }

body { background: #FFF; font-size: 50px; font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600))); line-height: 1.4; }

@media (max-width: 600px) { body { font-size: 16px; } }

@media (min-width: 1000px) { body { font-size: 50px; } }

.block { display: flex; }

@font-face { font-family: Bebas; src: url(../fonts/BEBAS.ttf); }

@font-face { font-family: HelveticaNeue; src: url(../fonts/HelveticaNeue.ttf); }

body { font-family: HelveticaNeue, sans-serif; color: #414141; }

h4 { font-family: Bebas; color: #e8603c; font-size: 18px; text-align: left; line-height: 1; font-weight: 100; margin-bottom: 12px; margin-top: 12px; }

h5 { font-family: Bebas; color: #e8603c; font-size: 14px; text-align: left; line-height: 1; font-weight: 100; padding-bottom: 4px; }

p { padding-bottom: 4px; }

a { color: #000; }

@font-face { font-family: Bebas; src: url(..fonts/BEBAS.ttf); }

.header { background-color: #fff; width: 80%; margin-left: 10%; }

.header ul { margin: 0; padding: 0; list-style: none; overflow: hidden; background-color: #fff; }

.header li a { display: block; font-size: 12px; text-decoration: none; font-size: 16px; font-family: Bebas; color: #e8603c; font-size: 16px; color: #b9b9b9; line-height: 1.2; padding: 12px; text-align: center; }

.header li a:hover { background-color: #f4f4f4; }

.header .menu-btn:hover { background-color: #f4f4f4; }

.header .header__actualpage { color: #e8603c; }

.header .header__logo { display: block; float: left; padding-left: 10px 20px; text-decoration: none; font-size: 40px; margin-left: 14px; }

.header .menu { clear: both; max-height: 0; transition: max-height .2s ease-out; }

.header .menu-icon { cursor: pointer; display: inline-block; float: right; padding: 28px 20px; position: relative; user-select: none; }

.header .menu-icon .navicon { background: #333; display: block; height: 2px; position: relative; transition: background .2s ease-out; width: 18px; }

.header .menu-icon .navicon:before, .header .menu-icon .navicon:after { background: #333; content: ''; display: block; height: 100%; position: absolute; transition: all .2s ease-out; width: 100%; }

.header .menu-icon .navicon:before { top: 5px; }

.header .menu-icon .navicon:after { top: -5px; }

.header .menu-btn { display: none; }

.header .menu-btn:checked ~ .menu { max-height: 312px; }

.header .menu-btn:checked ~ .menu-icon .navicon { background: transparent; }

.header .menu-btn:checked ~ .menu-icon .navicon:before { transform: rotate(-45deg); }

.header .menu-btn:checked ~ .menu-icon .navicon:after { transform: rotate(45deg); }

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before, .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after { top: 0; }

.header:after { content: ""; display: table; clear: both; }

@media (min-width: 48em) { .header li { float: left; }
  .header li a { padding: 20px 15px; }
  .header .menu { clear: none; float: right; max-height: none; }
  .header .menu-icon { display: none; } }

.categoryheader { width: 100%; background-color: #e8603c; height: 60px; font-size: 16px; display: flex; flex-direction: row; justify-content: space-between; }

.categoryheader__name { width: 80%; text-decoration: none; font-family: Bebas; color: #e8603c; font-size: 16px; color: white; padding: 16px 0px 0px 32px; }

.categoryheader__side { width: 9%; height: 100%; background-color: #f3af9d; }

.categoryheader_divider { width: 1%; height: 100%; background-color: white; }

.wrap { display: flex; flex-direction: column; width: 89%; margin-left: 2%; margin-top: 8px; }

.postarea__wrap { display: flex; flex-direction: row; }

.aside { min-height: 300px; margin-left: 1%; margin-top: 12px; font-size: 12px; padding-left: 24px; width: 100%; }

.postsarea { width: 90%; margin-left: 11%; min-height: 300px; margin-top: 12px; }

.postarea__slickimg { width: 100%; }

.postsarea__carousel { width: 100%; position: relative; z-index: 2; }

.carousel__wrap { display: flex; flex-direction: row; align-items: center; }

.icon-prev, .icon-next { fill: white; opacity: 0.8; font-size: 40px; }

.carousel__prev, .carousel__next { position: relative; z-index: 200; }

.carousel__prev { margin-right: -40px; }

.carousel__next { margin-left: -40px; }

.postsarea__stats { justify-content: space-around; color: #bababa; line-height: 1.667; padding-top: 24px; width: 25%; margin-right: 8px; }

.postsarea__text p { padding-bottom: 8px; }

.postsarea__postquote { font-size: 13px; color: #474747; font-style: italic; line-height: 1.385; text-align: left; background-color: #f5f5f5; padding: 16px 16px 16px 64px; }

.postsarea__link { margin: -6px 0px 15px 0; }

.postsarea__link a { text-decoration: none; font-size: 12px; color: #a0a0a0; line-height: 1.5; }

.postarea__divider img { width: 100%; }

.postarea__divider { margin-top: 50px; margin-bottom: 50px; }

.postarea__pagenation a { text-decoration: none; font-family: Bebas; color: #e8603c; font-size: 14px; color: white; line-height: 1.286; padding: 0px 9px 3px 9px; }

.color-muted { background-color: #f3af9d; }

.color-active { background-color: #e8603c; }

h5 img { display: none; }

@media (min-width: 48em) { .postsarea__stats { flex-direction: column; }
  .postsarea__text { padding: 10px; }
  .wrap { flex-direction: row; }
  .aside { width: 25%; margin-right: 1%; }
  .postsarea__post:before { content: url(../img/icon1.png); width: 75px; height: 75px; background-color: #e8603c; font-size: 35px; padding: 15px 25px 0px; position: absolute; margin-left: -87px; }
  .postsarea__post2:before { content: url(../img/icon2.png); }
  .postsarea__post3:before { content: url(../img/icon3.png); }
  .postsarea__post4:before { content: url(../img/icon4.png); } }

@media (min-width: 1024px) { h5 img { display: inline-block; } }

.postsarea__post { width: 100%; background-color: white; font-size: 12px; line-height: 1.5; text-align: left; }

.postsarea__img { width: 100%; }

.aside a { text-decoration: none; }

.aside h5 { padding-bottom: 16px; }

.short-line { float: right; overflow: hidden; }

.aside p, .aside ul { padding-bottom: 40px; }

.aside__category { color: #414141; }

.aside__galleryitem { height: 50px; width: 50px; background-color: #f3af9d; border: 1px solid #e8603c; margin: 2px; }

.aside__gallery { display: flex; flex-direction: column; width: 100%; }

.aside__galleryrow { display: flex; flex-direction: row; width: 80%; margin-left: 20%; }
